import React, { useContext, useEffect } from 'react';
import {
  BrowserRouter as Router,
  Route
} from 'react-router-dom'

import { Playlists, Auth, AuthContext, Layout } from '../index';
import spotifyAPI from '../../utils/spotify-api/spotifyAPI';
import PrivateRoute from '../PrivateRoute/PrivateRoute';

function App() {
  const authContext = useContext(AuthContext)
  const interceptor = spotifyAPI.interceptors.request.use(
    config => {
      if (authContext.isAuth) {
        const { token_type, access_token } = authContext.tokenProps
        config.headers['Authorization'] = `${token_type} ${access_token}`
      }
      return config
    },
    error => {
      console.log('config.message', error.message)
      return Promise.reject(error)
    }
  )

  useEffect(
    () => spotifyAPI.interceptors.request.eject(interceptor)
  )

  const protectedRoutes = (
    <React.Fragment>
      <PrivateRoute isAuthenticated={authContext.isAuth} path="/profile" component={() => <h1>Profile page</h1>} />
      <PrivateRoute isAuthenticated={authContext.isAuth} path="/playlists" component={Playlists} />
      <PrivateRoute isAuthenticated={authContext.isAuth} path="/home" component={() => <h1>Home page</h1>} />
    </React.Fragment>
  )

  console.log('en App')

  return (
    <Router basename="/my-spotify">
      <Layout>
        <Route path="/auth" component={Auth} />
        {protectedRoutes}
      </Layout>
    </Router>
  );
}

export default App;
