import React, { useEffect, useContext, useState } from 'react'
import querystring from 'query-string'
import { Typography, makeStyles } from '@material-ui/core'

import { AuthContext } from './AuthProvider/AuthProvider'
import spotifyAuth from '../../assests/images/spotifyAutha.jpg'
import { Dialog } from '../shared'

const useStyles = makeStyles({
    container: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        height: '90%'
    },
    card: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-around',
        alignItems: 'center',
        backgroundColor: 'black',
        padding: '15px'
    },
    img: {
        width: '100%',
        height: '70%',
        marginBottom: '5%'

    },
    divider: {
        border: '5px solid #4CAF50',
        width: '100%'
    },
    button: {
        backgroundColor: 'white',
        border: '2px solid #4CAF50',
        borderRadius: '30px',
        color: 'black',
        padding: '16px 32px',
        textAlign: 'center',
        textDecoration: 'none',
        display: 'inline-block',
        fontSize: '16px',
        margin: '30px 2px',
        transitionDuration: '0.4s',
        cursor: 'pointer',
        '&:hover': {
            backgroundColor: '#4CAF50',
            color: 'white'
        }
    }
})

const Auth = (props) => {
    const {location, history} = props

    console.log('auth props', props)
    const classes = useStyles()
    const authContext = useContext(AuthContext)
    const [authError, setAutheError] = useState()

    useEffect(() => {
        const spotifyTokenProps = querystring.parse(location.hash)
        const { error } = querystring.parse(location.search)

        console.log('auth props en effect 2 ', spotifyTokenProps)

        if (spotifyTokenProps.access_token && !authContext.isAuth) {
            console.log('auth en login', spotifyTokenProps)
            authContext.login(spotifyTokenProps)
            history.replace(localStorage.getItem("from") || '/playlists')
        } else if (error) {
            console.log('auth-error', error)
            setAutheError(error)
        }
    }, [authContext, location, history])

    const spotifyLoginURL = () => {
        const client_id = '7a66e558650c4b9d89018cadc7a94f21'//App id //https://mastrogithub.github.io/my-spotify/auth
        const redirect_uri = encodeURIComponent("https://mastrogithub.github.io/my-spotify/auth")//http://localhost:3000/my-spotify/auth
        const escopes = encodeURIComponent(["playlist-modify-public"].join(" "))
        return `https://accounts.spotify.com/authorize?client_id=${client_id}&scope=${escopes}&response_type=token&redirect_uri=${redirect_uri}`
    }

    const redirectToSpotifyHandler = () => {
        window.open(spotifyLoginURL(), '_self')
    }

    const onAcceptHandler = () => {
        setAutheError(null)
    }

    return (
        <React.Fragment>
            <Dialog
                open={!!authError}
                onAccept={onAcceptHandler}
                type="error"
                title="Authentication Error">
                <Typography>An authentication error has occurred.</Typography>
            </Dialog>
            <div className={classes.container}>
                <div className={classes.card}>
                    <img
                        className={classes.img}
                        src={spotifyAuth}
                        alt="spotifyImg" />
                    <hr className={classes.divider} />
                    <button
                        className={classes.button}
                        onClick={redirectToSpotifyHandler}>
                        Login with Spotify
                    </button>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Auth