import React, { useContext } from 'react'
import clsx from 'clsx';
import { AppBar, IconButton, Typography, makeStyles, Toolbar } from '@material-ui/core'
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

import { AuthContext } from '../../index';
import spotifyIcon from '../../../assests/images/spotifyIcon.png'

const useStyles = makeStyles(theme => ({
    appBar: {
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        width: `calc(100% - ${theme.drawerWidth}px)`,
        marginLeft: theme.drawerWidth,
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: theme.spacing(2)
    },
    hide: {
        display: 'none'
    },
    toolbar: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        backgroundColor: 'black'        
    },
    toogleSideDrawer: {
        display: 'flex',
        alignItems: 'center'
    },
    logout: {
        '&:hover': {
            cursor: 'pointer'
        }
    }
}));

const MyToolbar = (props) => {
    const { open, drawerWidth, onDrawerOpen } = props
    const classes = useStyles({ drawerWidth })
    const authContext = useContext(AuthContext);

    return (
        <AppBar
            position="fixed"
            className={clsx(classes.appBar, {
                [classes.appBarShift]: open,
            })}
        >
            <Toolbar className={classes.toolbar} >
                <div className={classes.toogleSideDrawer}>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        onClick={onDrawerOpen}
                        edge="start"
                        className={clsx(classes.menuButton, open && classes.hide)}
                    >
                        <img src={spotifyIcon} alt="spotifyIcon" />
                    </IconButton>
                    <Typography variant="h6" noWrap>
                        MySpotify
                    </Typography>
                </div>
                {authContext.isAuth && <ExitToAppIcon fontSize="large" onClick={authContext.logout} className={classes.logout} />}
            </Toolbar>
        </AppBar>
    )
}

export default MyToolbar