import React, { useState, useEffect, useCallback } from 'react'
import { useRouteMatch, Route, Switch, useHistory } from 'react-router-dom'
import { makeStyles } from '@material-ui/core'

import spotifyAPI from '../../utils/spotify-api/spotifyAPI'
import { withErrorHandler } from '../../hoc'
import { Backdrop, Card } from '../shared'
import track from '../../assests/images/track.jpg'
import Playlist from './Playlist/Playlist'

const useStyles = makeStyles({
  container: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap'
  }
})

const Playlists = (props) => {
  const classes = useStyles()
  const [playlists, setPlaylists] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  const [selectedPlaylist, setSelectedPlaylist] = useState()
  const { path } = useRouteMatch("/playlists")
  const history = useHistory();

  console.log('EN PLALISTSSSS', path)

  const fetchPlaylists = useCallback(() => {
    spotifyAPI.get(`/me/playlists`)
      .then(response => response.data.items)
      .then((items) => {
        setPlaylists(items)
        setIsLoading(false)
      })
      .catch(() => setIsLoading(false))
  }, [])

  useEffect(() => {
    console.log('PLAYLISTS EFFECT')
    fetchPlaylists()
  }, [fetchPlaylists])

  const selectedPlaylistHandler = (playlist) => {
    setSelectedPlaylist(playlist)
    history.push(`/playlists/${playlist.id}/tracks`)
  }

  const playlistCards = playlists.map(
    playlist => (
      <Card
        key={playlist.id}
        config={{
          image: { principal: playlist.images[0].url, default: track },
          title: playlist.name,
          description: `Owner: ${playlist.owner.display_name} - ${playlist.tracks.total} tracks`,
          clicked: () => selectedPlaylistHandler(playlist)
        }} />
    )
  )

  return (
    <Switch>
      <Route exact path={path} >
        <div className={classes.container}>
          <Backdrop open={isLoading} />
          {playlistCards}
        </div>
      </Route>
      <Route
        path={`${path}/:playlistId/tracks`}
        component={(routeProps) => <Playlist playlist={selectedPlaylist} {...routeProps} fetchPlaylists={fetchPlaylists} />} />
    </Switch>
  )
}

export default withErrorHandler(Playlists, spotifyAPI)