import React from 'react'

const withDroppable = (Component) => {
    return (props) => {

        const dropHandler = (event, callback) => {
            event.currentTarget.style.background = "lightyellow";

            event.preventDefault();
            var data = event.dataTransfer.getData("text");
            const draggableElement = document.getElementById(data)
            callback(draggableElement)
        }

        const dragoverHandler = (event) => {
            event.preventDefault();
        }
        
        return (
            <Component {...props} dropHandler={dropHandler} dragoverHandler={dragoverHandler} />
        )
    }
}

export default withDroppable