import React, { useState } from 'react';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Slide, makeStyles } from '@material-ui/core';
import ErrorIcon from '@material-ui/icons/Error';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const dialogTypes = (classes, title) => ({
    "error": {
        title: (
            <DialogTitle
                id="alert-dialog-slide-title"
                classes={{ root: classes.errorTitle }}>
                {title}
            </DialogTitle>
        ),
        icon: <ErrorIcon fontSize="large" />,

    }
})

const useStyles = makeStyles({
    errorTitle: {
        backgroundColor: '#ea5c5c'
    }
})

const MyDialog = ({ open, children, onAccept, onCancel, title, type, closeHandler }) => {
    const myClasses = useStyles()
    const [dialogConfig] = useState(dialogTypes(myClasses, title)[type])


    return (
        <Dialog
            open={open}
            TransitionComponent={Transition}
            keepMounted
            onClose={closeHandler}
            aria-labelledby="alert-dialog-slide-title"
            aria-describedby="alert-dialog-slide-description"
        >
            {dialogConfig.title}
            <DialogContent>
                {children}
            </DialogContent>
            <DialogActions>
                {
                    onCancel && <Button onClick={onCancel} color="primary">Disagree</Button>
                }
                {
                    onAccept && <Button onClick={onAccept} color="primary">Agree</Button>
                }
            </DialogActions>
        </Dialog>
    );
}


export default MyDialog