import React, { useState } from 'react'
import HomeIcon from '@material-ui/icons/Home';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import QueueMusicIcon from '@material-ui/icons/QueueMusic';

import NavigationItem from './NavigationItem/NavigationItem'

const itemsList = [
    {
        name: "home",
        to: "/home",
        icon: HomeIcon,
        primary: "Home"
    },
    {
        name: "profile",
        to: "/profile",
        icon: AccountCircleIcon,
        primary: "Profile"
    },
    {
        name: "playlists",
        to: "/playlists",
        icon: QueueMusicIcon,
        primary: "Playlists"
    }
]

const NavigationItems = () => {
    const [selectedItem, setSelectedItem] = useState()
    const [_itemsList] = useState(itemsList)

    const selectedItemHandler = (itemName, to) => {
        setSelectedItem(itemName)
        localStorage.setItem("from", to)//used after authentication redirect
    }

    return (
        <React.Fragment>
            {
                _itemsList.map((item) => (
                    <NavigationItem
                        key={item.name}
                        to={item.to}
                        icon={<item.icon htmlColor={selectedItem === item.name ? '#1ed760' : '#c1c3c6'} />}
                        primary={item.primary}
                        clicked={selectedItemHandler.bind(null,item.name, item.to)} />
                ))
            }
        </React.Fragment>
    )
}
export default NavigationItems