import React from 'react'
import { List } from '@material-ui/core'

import Track from './Track/Track';

const TrackList = ({list, ...rest}) => {

    console.log('EN TRACKLIST')

    return (
        <React.Fragment>
            <List dense={true}>
                {
                    list.map((track) => (
                        <Track key={track.id}{...track} {...rest}/>
                    ))
                }
            </List>

        </React.Fragment>
    )
}

export default React.memo(TrackList)