import React, { useReducer, useEffect } from 'react'


const AuthContext = React.createContext({
    isAuth: false,
    tokenProps: {},
    login: () => { },
    logout: () => { }
})

const reducer = (state, action) => {
    switch (action.type) {
        case 'LOGIN':
            return {
                isAuth: true,
                tokenProps: action.tokenProps
            }
        case 'LOGOUT':
            return {
                isAuth: false,
                tokenProps: {}
            }
        default:
            throw new Error();
    }
}

const initialState = { isAuth: false, tokenProps: {} }

const AuthContextProvider = (props) => {
    const [state, dispatch] = useReducer(reducer, initialState)

    useEffect(() => {
        let timer = null

        if (state.isAuth) {
            timer = setTimeout(logoutHandler, +state.tokenProps.expires_in * 1000)//logout after 1 hour
        }

        return () => clearTimeout(timer)
    }, [state])

    const loginHandler = (spotifyTokenProps) => {
        dispatch({ type: 'LOGIN', tokenProps: spotifyTokenProps })
    }

    const logoutHandler = () => {
        localStorage.removeItem("from")
        dispatch({ type: 'LOGOUT' })
    }

    return (
        <AuthContext.Provider value={{
            isAuth: state.isAuth,
            tokenProps: state.tokenProps,
            login: loginHandler,
            logout: logoutHandler
        }}>
            {props.children}
        </AuthContext.Provider>
    )
}

export {AuthContext, AuthContextProvider} 
