import React, { useEffect, useReducer } from 'react'
import { ListItem, makeStyles, ListItemIcon, ListItemText, ListItemSecondaryAction, IconButton } from '@material-ui/core'
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import DeleteIcon from '@material-ui/icons/Delete';
import PauseIcon from '@material-ui/icons/Pause';

import {withDraggable} from '../../../../hoc';

const useStyles = makeStyles(theme => ({
    listItem: {
        backgroundColor: 'transparent',
        '&:hover': {
            backgroundColor: '#f1c40f',
            cursor: 'pointer'
        }
    }
}))

const audioInitialState = {
    track: null,
    isPlaying: false
}

const audioReducer = (state, action) => {
    switch (action.type) {
        case 'SET_TRACK':
            return { track: action.track, isPlaying: true };
        case 'TOGGLE_PLAY':
            return { ...state, isPlaying: !state.isPlaying };
        case 'RESET':
            return init(action.payload);
        default:
            throw new Error();
    }
}

const  init = (audioInitialState) => ({...audioInitialState})

const Track = (props) => {
    const { id, name, artists: [artist], duration_ms, preview_url, deleteTrack, draggable, dragHandler, dragstartHandler } = props
    const classes = useStyles()
    const [audio, dispatchAudio] = useReducer(audioReducer, audioInitialState, init)

    const draggableProps = draggable && {
        onDrag: dragHandler,
        onDragStart: dragstartHandler
    }

    useEffect(() => {
        if (audio.track) {
            if (!audio.isPlaying) {
                audio.track.pause()
            } else {
                audio.track.play()
            }
        }

        return () => {
            console.log('salida effect')
            if (audio.track) {
                console.log('salida effect con audio', audio.track)
                audio.track.pause()
                dispatchAudio({ type: 'RESET', payload: audioInitialState })
            }
        }
    }, [audio])

    const playAudio = (previewUrl) => {//////////OPORTUNIDAD PARA APLICAR --->WHEN(predicate, fn())
        if (previewUrl) {
            if (!audio.track) {
                dispatchAudio({ type: 'SET_TRACK', track: new Audio(previewUrl) })
            } else {
                dispatchAudio({ type: 'TOGGLE_PLAY' })
            }
        }
    }

    return (
        <ListItem
            id={id}
            className={classes.listItem}
            draggable={draggable}
            {...draggableProps}
        >
            <ListItemIcon onClick={() => playAudio(preview_url)} >
                {!audio.track || !audio.isPlaying ? <PlayArrowIcon /> : <PauseIcon />}
            </ListItemIcon>
            <ListItemText
                primary={name}
                secondary={`${artist.name} - ${name}`}
            />
            <ListItemSecondaryAction>
                {(duration_ms / 60000).toFixed(2)}
                {
                    deleteTrack &&
                    <IconButton aria-label="delete" onClick={() => deleteTrack(id)} >
                        <DeleteIcon />
                    </IconButton>
                }
            </ListItemSecondaryAction>
        </ListItem>
    )
}

export default withDraggable(Track)