import React from 'react'
import Pagination from '@material-ui/lab/Pagination'

const withPagination = (Component, initialValues) => {
    const {visibleItems, array } = initialValues

    const COUNT = Math.floor(array.length / visibleItems) + (array.length % visibleItems !== 0 ? 1 : 0)

    return (props) => {
        const [page, setPage] = React.useState(1);
        const handleChange = (event, value) => {
            setPage(value);
        };

        const list = array.slice((page - 1) * visibleItems, page * visibleItems)

        return (
            <React.Fragment>
                <Component {...props} list={list}/>
                <Pagination count={COUNT} color="primary" page={page} onChange={handleChange} />
            </React.Fragment>
        )
    }
}

export default withPagination