import React, { useState, useCallback, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { IconButton, makeStyles } from '@material-ui/core'
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';

import spotifyAPI from '../../../utils/spotify-api/spotifyAPI'
import { Card } from '../../shared'
import track from '../../../assests/images/track.jpg'
import { withDroppable } from '../../../hoc';
import Recommendations from '../Recommendations/Recomendations';
import TrackList from '../TrackList/TrackList';

 import './Playlist.css'

const useStyles = makeStyles({
    container: {
        display: 'flex'
    },
    playlist: {
        flexBasis: '40%',
        justifyContent: 'center',
        display: 'flex',
        alignItems: 'flex-start'
    },
    tracks: {
        flexGrow: 1,
    },
    scrollY: {
        height: '250px',
        overflowY: 'scroll'          
    },
    title: {
        color: 'white'
    }
})

const Playlist = ({ playlist, ...props }) => {
    const classes = useStyles()
    const [_playlist, setPlaylist] = useState(playlist)
    const { playlistId } = useParams()
    const [tracks, setTracks] = useState([])

    const fetchTracks = useCallback(async () => {
        try {
            const response = await spotifyAPI.get(`/playlists/${playlistId}/tracks`)
            const _tracks = response.data.items.map(item => item.track)
            setTracks(_tracks)
        } catch (error) {
            console.log('error en tracks')
        }
    }, [playlistId])

    useEffect(() => {
        fetchTracks()
    }, [fetchTracks])

    const postTrackHandler = async (draggableElement) => {
        if (draggableElement) {
            const { id } = draggableElement
            await spotifyAPI.post(`/playlists/${playlistId}/tracks`, {
                uris: [`spotify:track:${id}`]
            })

            fetchTracks()
            setPlaylist(updateNroTracks(1))
        }
    }

    const deleteTrackHandler = useCallback((id) => {
        spotifyAPI.delete(`/playlists/${playlistId}/tracks`, {
            data: {
                tracks: [{ uri: `spotify:track:${id}` }]
            }
        })
            .then(response => {
                const index = tracks.findIndex(track => track.id === id)
                const _tracks = [...tracks]
                _tracks.splice(index, 1)
                setTracks(_tracks)
                setPlaylist(updateNroTracks(-1))
            })
    }, [tracks, playlistId])

    const updateNroTracks = nro => playlist => ({
        ...playlist,
        tracks: {
            ...playlist.tracks,
            total: playlist.tracks.total + nro
        }
    })

    const goBackHandler = () => {
        props.fetchPlaylists()
        props.history.goBack()
    }

    return (
        <React.Fragment>
            <IconButton color="secondary" aria-label="back" onClick={goBackHandler} >
                <ArrowBackIosIcon />
            </IconButton>
            <div className={classes.container}>
                <div className={classes.playlist}>
                    <Card
                        onDrop={(event) => props.dropHandler(event, postTrackHandler)}
                        onDragOver={props.dragoverHandler}
                        key={_playlist.id}
                        config={{
                            image: { principal: _playlist.images[0].url, default: track },
                            title: _playlist.name,
                            description: `Owner: ${_playlist.owner.display_name} - ${_playlist.tracks.total} tracks`,
                            clicked: () => { }
                        }} />
                </div>
                <div className={classes.tracks}>
                    <h3 className={classes.title} >Your Tracklist</h3>
                    <div id="trackList" className={classes.scrollY}>
                        <TrackList list={tracks} draggable={false} deleteTrack={deleteTrackHandler} />
                    </div>

                    <div>
                        <Recommendations />
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default withDroppable(Playlist)