import React, { useState, useEffect } from 'react'

import spotifyAPI from '../../../utils/spotify-api/spotifyAPI'
import TrackListWithPagination from '../TrackList/TrackListWithPagination/TrackListWithPagination';


const Recommendations = () => {
    const [recommendations, setRecommnendations] = useState([])

    console.log('EN RECOMENDACIONES')

    useEffect(() => {
        const fetchRecommendations = async () => {
            const seed_genres = ["alt_rock", "bluegrass", "blues", "classical", "country"].join(',')
            const response = await spotifyAPI.get(`/recommendations?seed_artists=&seed_genres=${seed_genres}&seed_tracks=&limit=100`)
            console.log('recomendations', response.data.tracks)
            setRecommnendations(response.data.tracks)
        }
        fetchRecommendations()
    }, [])

    return (
        <React.Fragment>
            <h3 style={{ color: 'white' }}>Recommended Songs</h3>
            <TrackListWithPagination visibleItems={5} array={recommendations} draggable />
        </React.Fragment>

    )
}

export default React.memo(Recommendations)